<template>
  <div>
    <h3> 运营团队列表</h3>
    <Divider orientation="left">列表筛选</Divider>
    <Row class="search">
      <Form inline :model="formData" :label-width="80">
        <FormItem label="编号">
          <Input v-model="formData.id" placeholder="请输入编号" class="filter-input"></Input>
        </FormItem>
        <FormItem label="名称">
          <Input v-model="formData.name" placeholder="请输入名称" class="filter-input"></Input>
        </FormItem>

        <FormItem label="状态">
          <Select class="filter-input" v-model="formData.status" placeholder="请选择">
            <Option
              label="正常"
              :value="1">
            </Option>
            <Option
              label="禁用"
              :value="0">
            </Option>
          </Select>
        </FormItem>
        <FormItem label="负责人">
          <Select v-model="formData.leader_id" class="filter-input" placeholder="请选择">
            <Option v-for="(item,index) in leaders" :label="item.nickname" :value="item.id" v-bind:key="index"></Option>
          </Select>
        </FormItem>
      </Form>
      <div class="filter-btn">
        <span class="span">操作 </span>
        <Button style="font-weight: bold" type="info" icon="ios-search" @click="loadData(1)"> 搜索</Button>
        <Button style="font-weight: bold" type="warning" icon="md-refresh" @click="ref()">重置</Button>
        <Button style="font-weight: bold" type="success" icon="md-add" @click="add()">新建</Button>
      </div>
    </Row>
    <Divider orientation="left">列表数据</Divider>
    <Table stripe border :columns="columns" :data="listData.data"></Table>
    <Spin size="large" fix v-if="load"></Spin>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="parseInt(listData.per_page)"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from '../operating/api'

export default {
  name: 'index',
  data () {
    return {
      formData: {},
      listData: {},
      leaders: [],
      load: false,
      columns: [
        {
          title: 'id',
          align: 'center',
          key: 'id'
        },
        {
          title: '名称',
          align: 'center',
          key: 'name'
        },
        { title: '创建日期', align: 'center', key: 'create_time' },
        {
          title: '负责人',
          align: 'center',
          key: 'leader',
          render: (h, params) => {
            return h('span', params.row.leader ? params.row.leader.nickname : '')
          }
        },
        {
          title: '运营模式',
          align: 'center',
          key: 'management_type',
          render: (h, params) => {
            return h('span', params.row.management_type === 0 ? '联合运营' : '自营')
          }
        },
        {
          title: '空间',
          align: 'center',
          key: 'space',
          render: (h, params) => {
            return h('span', params.row.space ? params.row.space.name : '')
          }
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 0 ? '禁用' : '正常')
          }
        },
        {
          title: 'Action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.add(params.row.id)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.del(params.row)
                  }
                }
              }, '删除')
            ])
          }
        }

      ]
    }
  },
  activated () {
    this.loadData()
    this.loadLeader()
  },
  methods: {
    loadData (page) {
      if (page) {
        this.formData.jump_page = page
      }
      if (this.formData.registerTime != null && this.formData.registerTime.length === 2) {
        this.formData.register_start = this.formData.registerTime[0]
        this.formData.register_end = this.formData.registerTime[1]
      } else {
        this.formData.register_start = null
        this.formData.register_end = null
      }

      if (this.formData.loginTime != null && this.formData.loginTime.length === 2) {
        this.formData.login_start = this.formData.loginTime[0]
        this.formData.login_end = this.formData.loginTime[1]
      } else {
        this.formData.login_start = null
        this.formData.login_end = null
      }

      this.load = true
      api.index(this.formData, res => {
        this.listData = res.data.data
        this.load = false
      }, () => {
        this.load = false
      })
    },
    loadLeader () {
      api.leader({ operation_team_id: 0 }, res => {
        this.leaders = res.data.data
      })
    },
    add (id) {
      this.$router.push({
        name: 'operationEdit',
        query: {
          id: id
        }
      })
    },
    ref () {
      this.formData = {}
      this.loadData(1)
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除' + item.name + '吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData(1)
          }, () => {
            this.$Modal.remove()
          })
        }
      })
    }
  }
}
</script>
